.posts-wrapper {
  background-color:papayawhip;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 10px;
  padding-right: 10px;
}

.post-wrapper {
    background-color: #2a073386;
    word-break: break-word;
    flex-shrink: 0;
    width: 600px;
    max-width: 100%;
    height: auto;
    margin-top: 10px;
    border-radius: 5px;
  
}
  
.author {
    display: flex;
}

.text {
    color: aliceblue;
    padding-top: 1%;
    font-size: 1em;
    font-weight: bold;
}

.logo {
    flex-shrink: 0;
    height: 60px;  
    overflow: hidden;
    margin: 20px;
    border-radius: 50%;
}


.img-post {  
    left: 90px;
    width: 80%;
    border-radius: 5%;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 5px;

}

.content-text {
    font-weight: bold;
}

.nickname-date {
    color: rgba(240, 248, 255, 0.5);
}
/* 
@media (max-width: 992px) {
    .logo {
        height: 40px; ;
    }
} */

.icon {
    width: 20px;
    height: 20px;
    margin-right: 5px;
    cursor: pointer;
    stroke: cornflowerblue;
}

/* .icon:active  {
    fill: chocolate;
} */

.posts-interaction-wrapper {
    display: flex;
    justify-content: space-between; 
    margin: 10px 40px 15px 0;
}

.posts-interaction-items {
    flex-shrink: 0;
    cursor: pointer;
    color:papayawhip;
}

/*  New post form styles  */

.new-post-form-wrapper {
    width: 600px;
    max-width: 100%;
}

.inputs-wrapper {
    width: 100%;
    background-color: #2a073386;
    word-break: break-word;
    flex-shrink: 0;
    margin-top: 10px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.new-post-btn-wrapper {
    padding-top: 20px;
    display: flex;

}

.new-post-btn {
    cursor: pointer;
    border-radius: 5px;
    background-color:papayawhip;
    padding: 5px;
}

.new-user-label {
    margin-right: 10px;
}

.new-user-post{
    display: flex;
    flex-direction: column;
    width:100%;
    padding: 0 10px 0 10px;
}


.new-post-items {
    border-radius: 5px;
    outline-style: none;
    width: 100%;
}

.image-link{
    margin: 5px 0 5px 0;
}

.new-user-option .image-link {
    padding: 5px;
}